.about-hotel {
  max-width: 1280px;
  margin: 0 auto;
  height: 1191px;
  max-height: 100%;
  padding-top: 18px;
  padding-bottom: 72px;

  background-color: $logo-header-room-color;

  @include desktop {
    min-height: 1275px;
    padding-top: 120px;
    padding-bottom: 122px;
  }

  @include tablet {
    min-height: 1238px;
    padding-top: 120px;
    padding-bottom: 96px;
  }

  &__container-hotel {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;

    @include mobile {
      max-width: 414px;
    }

    @include tablet {
      max-width: 1024px;
    }

    @include desktop {
      max-width: 100%;     
    }
  }

  &__title-about {
    margin-left: auto;
    margin-right: 0;
    margin-bottom: 6px;

    font-family: 'Times New Roman';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.16;
    color: $subtitles-color;

    @include tablet {
      margin-left: 660px;
      margin-right: 0;
      margin-bottom: 4px;
      width: 307px;
      font-size: 32px;
    }

    @include desktop {
      margin-left: 880px;
      margin-right: 0;
      margin-bottom: 4px;
      width: 304px;
      font-size: 32px;
    }
  }

  &__logo-about {
    margin-left: auto;
    margin-right: 0;
    margin-bottom: 16px;
    font-family: 'Film P3';
    font-weight: 700;
    font-size: 40px;
    line-height: 1.075;
    letter-spacing: 0.3em;
    color: $logo-rest-hotel-color;
    text-decoration: none;
    text-transform: uppercase;

    @include tablet {
      margin-left: 765px;
      margin-right: 0;
      font-size: 64px;
      line-height: 1.078;
      margin-bottom: 24px;
    }

    @include desktop {
      margin-left: 980px;
      margin-right: 0;
      margin-bottom: 24px;
      font-size: 64px;
      line-height: 1.078;
    }
  }

  &__text-couch {
    margin-left: auto;
    margin-right: 0;
    margin-bottom: 90px;
    max-width: 242px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 1, 25;
    letter-spacing: 0.02em;
    color: $text-rest-homepage-color;

    @include tablet {
      margin-left: 550px;
      margin-right: 0;
      margin-bottom: 45px;
      min-width: 419px;
      font-size: 16px;
    }

    @include desktop {
      margin-left: 695px;
      margin-right: 0;
      margin-bottom: 45px;
      min-width: 488px;
      font-size: 16px;
    }
  }

  &__photo-apartment {
    position: absolute;
    top: 183px;
    left: 15px;
    display: block;
    width: 244px;
    margin-right: auto;
    margin-bottom: 32px;
    background: $background-img-color;
    box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
    border-radius: 2px;
    z-index: 1;

    @include tablet {
      position: absolute;
      top: -150px;
      left: 112px;
      width: 400px;
      margin-bottom: 0;
    }

    @include desktop {
      width: 460px;
      height: 574px;
      top: -150px;
      left: 180px;
    }
  }
}

.hotel-hall {
  display: flex;
  align-items: center;
  margin-top: 280px;

  @media screen and (min-width: 1024px) {
    margin-bottom: 262px;
    width: 710px;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 207px;
    width: 804px;
  }

  &__text-hall {
    // margin-top: 350px;
    width: 163px;
    height: 100%;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    letter-spacing: 0.02em;
    color: $second-accent-color;

    @include tablet {
      margin-left: 220px;
      margin-bottom: 154px;
      min-width: 312px;
      font-size: 16px;
    }

    @include desktop {
      margin-left: 365px;
      margin-bottom: 116px;
      min-width: 311px;
      font-size: 16px;
    }
  }

  &__photo-hall {
    margin-bottom: 32px;
    margin-left: 21px;
    min-width: 200px;

    background: $background-img-color;
    box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
    border-radius: 2px;

    @include tablet {
      position: absolute;
      top: 325px;
      right: 47px;
      width: 400px;
      margin-bottom: 0;
    }

    @include desktop {
      position: absolute;
      width: 460px;
      height: 574px;
      top: 360px;
      right: 85px;
    }
  }
}

//  Slider

.about-hotel__slider-wrapper {
  position: absolute;
  top: 856px;
  right: 84px;
  width: 330px;
  border-radius: 2px;
  overflow: hidden;
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.15);

  & .slick-track {
      display: flex;
      gap: 20px;
      margin-left: 85px;
    }

    @media screen and (min-width: 1024px) {
      top: 830px;
      right: 549px;
      width: 428px;

      & .slick-track {
          gap: 40px;
          margin-left: 0px;
        }
    }

    @media screen and (min-width: 1280px) {
      top: 760px;
      right: 665px;
      width: 530px;
    }

    & .slick-list {
      overflow: hidden;
    }
}

.about-hotel__slick-item:nth-child(1n) {
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.20);

  @media screen and (min-width: 1024px) {
    box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  }
}

.about-hotel__slider-nav {
  position: absolute;
  bottom: -301px;
  left: 16px;
  width: 94px;
  height: 18px;

  @media screen and (min-width: 1024px) {
    justify-content: space-between;
    bottom: 4px;
    left: 48px;
  }

  @media screen and (min-width: 1280px) {
    justify-content: space-between;
    bottom: -88px;
    left: 90px;
  }

  & .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-9px);
    width: 30px;
    height: 18px;
    z-index: 10;
    font-size: 0;
    border: none;

    &.slick-prev {
      left: 0;
      background-image: url('../images/svg/slider-prev-button.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      background-color: transparent;
      cursor: pointer;
    }

    &.slick-next {
      right: 0;
      background-image: url('../images/svg/slider-next-button.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      background-color: transparent;
      cursor: pointer;
    }
  }
}

.background-gradient {
 background-image: linear-gradient(to right,
      $background-light-color 138px,
      $bg-gradient-color 139px,
      $background-light-color 140px,
      $background-light-color 273px,
      $bg-gradient-color 274px,
      $background-light-color 275px);

  @media screen and (min-width: 1024px) {
    max-width: 1024px;
    height: 1238px;
    background-image: linear-gradient(to right,
        $background-light-color 278px,
        $bg-gradient-color 279px,
        $background-light-color 280px,
        $background-light-color 511px,
        $bg-gradient-color 512px,
        $background-light-color 513px,
        $background-light-color 745px,
        $bg-gradient-color 746px,
        $background-light-color 747px);
  }

  @media screen and (min-width: 1280px) {
    max-width: 1280px;
    height: 1257px;
    background-image: linear-gradient(to right,
        $background-light-color 338px,
        $bg-gradient-color 339px,
        $background-light-color 340px,
        $background-light-color 639px,
        $bg-gradient-color 640px,
        $background-light-color 641px,
        $background-light-color 941px,
        $bg-gradient-color 942px,
        $background-light-color 943px);
  }
}
